/**
 * hornet-js-react-components - Ensemble des composants web React de base de hornet-js
 *
 * @author MEAE - Ministre de l'Europe et des Affaires trangres
 * @version v5.2.4
 * @link git+https://github.com/diplomatiegouvfr/hornet-js.git
 * @license CECILL-2.1
 */

import { Utils } from "hornet-js-utils";
import { ArrayUtils } from "hornet-js-utils/src/array-utils";
import { Logger } from "hornet-js-utils/src/logger";

import * as React from "react";
import * as classNames from "classnames";

import { HornetComponent } from "src/widget/component/hornet-component";
import { HornetComponentProps } from "hornet-js-components/src/component/ihornet-component";
import { MenuActions } from "src/widget/table/menu-actions";
import { ToggleColumnsButton } from "src/widget/table/toggle-columns-button";
import { TableState, ContentState } from "src/widget/table/table-state";
import { Confirm } from "src/widget/dialog/confirm";
import { PaginateDataSource, Pagination } from "hornet-js-core/src/component/datasource/paginate-datasource";
import { DataSource } from "hornet-js-core/src/component/datasource/datasource";

const logger: Logger = Utils.getLogger("hornet-js-components.widget.table.header");

/**
 * Proprits du composant Header de tableau Hornet
 */
export interface HeaderProps extends HornetComponentProps {
    /**  Identifiant unique */
    id?: string;
    /** Identifiant du parent */
    parentId?: string;
    /** Titre du Table */
    title: string;
    /** message qui affiche le nombre total d'item dans le header */
    libelleNombreTotalItem?: string;
    /** Affichage du bouton d'accessibilit */
    showIconInfo?: boolean;
    /** Affichage du bouton d'accessibilit et du menu action*/
    hideMenuactions?: boolean;
    /** Affichage de l'action permettant de masquer/afficher les colonnes **/
    toggleColumns?: boolean;

    /** Informations de colonnes */
    columns?: any;
    /** Elements du table */
    items?: any[];
    /** Elements slectionns du Table */
    selectedItems?: number[];
    tableState?: TableState;
    /** content state */
    contentState?: ContentState;
    /** liste des PaginateDataSource de tous les <Content.tsx> du composant table, s'ils exitent */
    dataSourcesList?: PaginateDataSource<any>[];
}

/**
 * Header de tableau
 */
export class Header extends HornetComponent<HeaderProps, any> {

    protected headerRef: any;
    protected hiddenColumns: any;

    constructor(props: HeaderProps, context?: any) {
        super(props, context);

        this.state = {
            ...this.state,
            id: this.state.id || this.state.parentId,
            libelleNombreTotalItem: this.state.libelleNombreTotalItem || "table.numberElementTitle",
            items: [],
            selectedItems: [],
        };

        // gestion de l'event de changement de la liste des items du tableau
        this.handleChangeDataTable = this.handleChangeDataTable.bind(this);
        this.props.tableState.on(TableState.INDEX_CHANGE_EVENT, this.handleChangeDataTable);
        // gestion de l'event d'edidtion du tableau
        this.props.contentState.setMaxListeners(Infinity);
        this.handleEdition = this.handleEdition.bind(this);
        this.props.contentState.on(ContentState.EDITION_CLIC_EVENT, this.handleEdition);
        this.props.contentState.on(ContentState.EDITION_CLIC_EVENT, this.handleEdition);
        this.hiddenColumns = (props as any).hiddenColumns;
        (props as any).contentState.on(ContentState.TOGGLE_COLUMNS_EVENT, this.handleChangeHiddenColumns);
    }

    componentWillUnmount() {
        this.props.tableState.removeListener(TableState.INDEX_CHANGE_EVENT, this.handleChangeDataTable);
        this.props.contentState.removeListener(ContentState.EDITION_CLIC_EVENT, this.handleEdition);
        if (this.props.dataSourcesList) {
            this.props.dataSourcesList.map((dataSource, index) => {
                this.props.dataSourcesList[index].removeListener("select", this.handleChangeDataTable);
                this.props.dataSourcesList[index].removeListener("pagination", this.handlePaginationChange);
                this.props.dataSourcesList[index].removeListener("fetch", this.handlePaginationChange);
            });
        }
    }

    componentDidMount(): void {
        // gestion de l'event de selection des lignes du tableau
        // on s'abonne au select du dataSource de chaque content
        if (this.props.dataSourcesList && Array.isArray(this.props.dataSourcesList) && this.props.dataSourcesList.length > 0) {
            this.props.dataSourcesList.map((dataSource, index) => {
                this.props.dataSourcesList[index].setMaxListeners(Infinity);
                this.props.dataSourcesList[index].on("select", this.handleChangeDataTable);
                this.props.dataSourcesList[index].on("pagination", this.handlePaginationChange);
                this.props.dataSourcesList[index].on("fetch", this.handlePaginationChange);
            });
        }
        this.props.tableState.emit(TableState.RESIZE_EVENT, this.headerRef.clientWidth);
    }

    /**
     * @inheritDoc
     */
    render(): JSX.Element {
        logger.debug("Header render : ", this.state.id);

        const headerContainerProps = {
            id: this.state.id,
            className: classNames({
                "datatable-header-title": true,
                "flex-container": true,
                "badge-selected-items-before": this.state.contentState.hasCheckColumnMassSelection && this.getTotalSelectedItemsForAllDataSource() !== 0,
            }),
            "data-badge": this.getTotalSelectedItemsForAllDataSource(),
            tabIndex: this.state.tabIndex,
        };

        return (

            <div {...headerContainerProps} ref={(instance) => { this.headerRef = instance; }}>
                <div className="datatable-title">
                    <span className="datatable-title-span">
                        {this.state.title + " " + this.i18n(this.state.libelleNombreTotalItem,
                            { count: this.getTotalItemsForAllDataSource(), ...this.getPaginationForFirstDataSource() })}
                    </span>
                </div>
                {this.renderOthersChildren(true)}
                {(!this.state.hideMenuActions) ? this.renderMenuActions() : null}
                {this.renderOthersChildren(false)}
                <Confirm ref="alert" message={""}
                    onClickCancel={this.closeAlert}
                    onClickClose={this.closeAlert} />
            </div>);
    }

    /**
     * Mthode permettant de mettre  jour le nombre de colonnes
     * @param hiddenColumns
     */
    handleChangeHiddenColumns(hiddenColumns) {
        this.hiddenColumns = hiddenColumns;
    }

    /**
     * Rendu HTML du menu des actions
     * @returns {any}
     */
    renderMenuActions(): JSX.Element {
        logger.trace("renderMenuActions");
        const children: any = this.getChildrenOf(MenuActions);
        let actions: any[] = [];

        // cas ou une seule action est dclare
        if (children.props && !children.length) {
            actions.push(children);
        } else {
            actions = children;
        }

        // Dtection de la prsence du toggleColumnsButton
        const toggleColumnsButton: any = this.getComponentBy(ToggleColumnsButton);

        let WrappedToggleColumns = null;
        if (toggleColumnsButton) {
            const key: string = this.state.id + "toggleColumnsButton";
            const props = {
                ...toggleColumnsButton.props,
                id: this.state.id,
                key,
                tabIndex: -1,
                columns: this.props.columns,
                contentState: this.props.contentState,
                hiddenColumns: this.hiddenColumns
            };
            WrappedToggleColumns = React.createElement(ToggleColumnsButton, props);
        }

        const menuActionsProps: any = {
            actions,
            items: this.state.items,
            showAlert: this.showAlert,
            showIconInfo: this.props.showIconInfo,
            selectedItems: this.getAllSelectedItems(),
            id: this.state.id + "-menu-action",
            columns: this.props.columns,
            toggleColumnsButton: WrappedToggleColumns,
            contentState: this.props.contentState,
        };

        return (
            <MenuActions {...menuActionsProps} />
        );
    }

    /**
     * Render des noeuds fils de Header qui ne sont pas des MenusAction ni des ToggleColumnsButton
     * @param beforeMenuAction indique s'il faut rendre les noeuds fils du header qui sont placs avant ou aprs le noeud MenuActions
     */
    protected renderOthersChildren(beforeMenuAction: boolean) {
        logger.trace("renderOthersChildren. Noeuds avant MenuAction ? ", beforeMenuAction);
        const othersChildren = [];
        let isMenuActionFound: boolean;
        React.Children.map(this.props.children, (child: React.ReactChild, index) => {
            if (child && (child as React.ReactElement<any>).type !== MenuActions && (child as React.ReactElement<any>).type !== ToggleColumnsButton) {
                const menuActionsProps: any = {
                    items: this.state.items,
                    showAlert: this.showAlert,
                    showIconInfo: this.props.showIconInfo,
                    selectedItems: this.getAllSelectedItems(),
                    id: this.state.id + "-custom-header-" + index,
                    key: this.state.id + "-custom-header-key-" + index,
                    columns: this.props.columns,
                    contentState: this.props.contentState
                };
                if ((beforeMenuAction && !isMenuActionFound) || (!beforeMenuAction && isMenuActionFound)) {
                    othersChildren.push(React.createElement((child as React.ReactElement<any>).type, { ...menuActionsProps, ...(child as any).props }));
                }
            } else if ((child as React.ReactElement<any>).type === MenuActions) {
                isMenuActionFound = true;
            }
        });
        return othersChildren && othersChildren.length > 0 ?
            othersChildren : null;
    }

    /**
     * cache le menuActions lorsque la table est en cours d'edition.
     * @param lineIndex
     */
    handleEdition(lineIndex: number): void {
        this.setState({ hideMenuActions: lineIndex !== undefined && lineIndex !== null });
    }

    /**
     *
     * @param selectedItems
     * @param items
     */
    handleChangeDataTable(selectedItems: any[], items?: any[]) {
        this.setState({ selectedItems, items: items ? items : this.state.items });
    }

    /**
     * Mthode dclenchant la fermeture de la fentre modale de suppresion d'un partenaire
     */
    protected closeAlert(): void {
        (this.refs.alert as Confirm).close();
    }

    /**
     * Mthode dclenchant la fermeture de la fentre modale de suppresion d'un partenaire
     */
    protected validateAlert(fct?: Function): void {
        (this.refs.alert as Confirm).close(fct);
    }

    /***
     * Dclenche l'affichage de la modale de suppression d'un partenaire
     * @param message
     * @param title
     * @param {Function} fct fonction excute sur la validation
     */
    protected showAlert(message: string, title: string, fct: Function): void {
        (this.refs.alert as Confirm).setMessage(message);
        (this.refs.alert as Confirm).setTitle(title);
        (this.refs.alert as Confirm).setOnClickOk(() => {
            this.validateAlert(fct);
        }).open();
    }

    /**
     * fonction qui retourne la liste des items selections sur l'ihm lors de la pagination
     * @returns {any[]}
     */
    protected getSelectedItemsForAllContent(): any[] {
        logger.trace("getSelectedItemsForAllContent");
        let resultList: any[] = [];
        // intersection des items affichs avec les items selections dans le dataSource
        resultList = this.getAllSelectedItems();
        resultList = ArrayUtils.intersectionWith(resultList, this.state.items, this.state.contentState.keyColumnMassSelection);
        return resultList;
    }

    /**
     * fonction qui retourne la liste des items selections
     * @returns {any[]}
     */
    protected getAllSelectedItems(): any[] {
        logger.trace("getSelectedItems");
        let resultList: any[] = [];
        // recupere la liste de tous les items selections dans les dataSources des contents
        this.props.dataSourcesList.map((dataSource, index) => {
            resultList = ArrayUtils.unionWith(this.props.dataSourcesList[index].selected, resultList, this.state.contentState.keyColumnMassSelection);
        });
        // intersection des items affichs avec les items selections dans le dataSource
        return resultList;
    }

    /**
     * Retourne la somme totale des items de tous les dataSource de tous les contents
     * @returns {number}
     */
    protected getTotalItemsForAllDataSource(): number {
        logger.trace("getTotalItemsForAllDataSource");
        let result: number = 0;
        this.props.dataSourcesList.map((dataSource: DataSource<any>) => {
            let nbItem: number = 0;

            if (dataSource) {
                // si le dataSource est de type PaginateDataSource, on prend le totalItems sinon on prend le result.length
                if (dataSource instanceof PaginateDataSource) {
                    const pagDt: PaginateDataSource<any> = dataSource as PaginateDataSource<any>;
                    nbItem = pagDt && pagDt.pagination && pagDt.pagination.totalItems ? pagDt.pagination.totalItems : 0;
                } else {
                    nbItem = dataSource.results ? dataSource.results.length : 0;
                }
                result += nbItem;
            }
        });

        return result;
    }

    /**
 * Retourne la somme totale des items de tous les dataSource de tous les contents
 * @returns {number}
 */
    protected getPaginationForFirstDataSource(): {} {
        logger.trace("getPaginationForFirstDataSource");
        let result: Pagination = {} as any;
        this.props.dataSourcesList.every((dataSource: DataSource<any>) => {
            if (dataSource) {
                // si le dataSource est de type PaginateDataSource, on prend le totalItems sinon on prend le result.length
                if (dataSource instanceof PaginateDataSource) {
                    const pagDt: PaginateDataSource<any> = dataSource as PaginateDataSource<any>;
                    result = { ...pagDt.pagination };
                    if (!result.nbPages || result.nbPages === 0) {
                        result.nbPages = Math.max(1, Math.ceil(result.totalItems / result.itemsPerPage)) || 0;
                    }
                    return false;
                }
            }
        });

        return result;
    }

    /**
     * Retourne la somme totale des items de tous les dataSource de tous les contents
     * @returns {number}
     */
    protected getTotalSelectedItemsForAllDataSource(): number {
        logger.trace("getTotalSelectedItemsForAllDataSource");
        let result: number = 0;
        this.props.dataSourcesList.map((dataSource: DataSource<any>) => {
            if (dataSource) {
                // si le dataSource est de type PaginateDataSource, on prend le totalItems sinon on prend le result.length
                result += (dataSource.selected) ? dataSource.selected.length : 0;
            }
        });
        return result;
    }

    handlePaginationChange(result): void {
        this.setState({ pagination: _.cloneDeep(result.pagination) });
    }
}



